@import '../../variables.scss';

.events-container {
    position: relative;
    flex-direction: column;
    gap: 1rem;
    width: 70%;
    height: 19rem;
    padding: .5rem;
    border-radius: $border-radius-common-block;
    background-color: #ffffff7c;
    @include box-shadow-standard;
    
    h2,
    .events-item,
    .ant-empty {
        background-color: $color-white;
        border-radius: $border-radius-common-block;
    }

    .ant-empty {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        padding: 1rem;
    }

    .events-wrapper {
        height: 18.5rem;
        overflow-y: auto;
        padding-bottom: 1rem;
        
        .events-header {
            width: max-content;
            margin: 0 auto;
            
            h2 {
                @include flex-all-centered;
                text-align: center;
                min-height: 2.5rem;
                max-width: max-content;
                margin-top: .5rem;
                padding: .2rem 1rem;
                gap: 1rem;
                font-size: 1rem;
            }
        }   

        .events-items {
            .events-item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: .5rem;
                min-height: max-content;
                margin-top: 1rem;
                
                span {
                    font-size: .9rem;
                    padding: 0 .5rem;
                    text-align: start;
                    min-width: 6.5rem;
                    text-align: center;
        
                    &.event-title {
                        text-align: start;
                        margin-right: auto;
                    }
                }
            }
        }
    }

    &.justify-center {
        justify-content: center;
    }

    button {
        @include update-button;
        position: absolute;
        right: -2%;
    }

    @media (max-width: $window-l) {
        width: $size-l;

        button {
            right: -5%;
        }
    }
    
    @media (max-width: $window-m) {
        width: $size-m;
    }

    @media (max-width: $window-s) {
        width: $size-s;

        .events-wrapper {
            .events-items {
                .events-item span {
                    padding: 0;
                    min-width: 6rem;
                    font-size: .8rem;
                }
            }
        }
    }
}