@import '../../variables.scss';

.modal-container {
    display: block;
    width: 100%;
    height: 100%;

    .modal-window {
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        position: fixed;
        width: 25rem;
        height: $size-s;
        border-radius: 2rem;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        padding: 2rem;
        z-index: 100;
        opacity: 1;
        box-shadow: 24px 19px 8px 0px rgba(34, 60, 80, 0.2);
        background-color: #ff5f5fbb;
        font-size: 1.2rem;
        text-align: center;

        .modal-content {
            display: flex;
            flex-direction: column;
            gap: 1rem;
        }

        .modal-buttons {
            display: flex;
            gap: 1rem;
            button {
                min-width: 5rem;
    
                &:hover {
                    border-color: $color-red;
                    color: $color-red;
                }
            }
        }   
    }

    @media (max-width: $window-s) {
        .modal-window {
            width: $size-m;
            height: $size-l;
        }
    }
}